/* ============ VARIABLES CSS ============ */
:root {
  --header-height: 3rem;
  /* ============ Colors ============ */
  /* Change favorite color to match images */
  /* Green dark 190 - Green 171 - Green Blue 200 */
  --hue-color: 190;
  /* HSL color mode */
  --first-color: #055052;
  --second-color: #53b8bb;
  --first-color-alt: #003638;
  --first-color-shadow: rgba(5, 80, 82, 0.2);
  --light-color: #f3f2c9;
  --gray-color: #b8c4c7;
  --title-color: hsl(var(--hue-color), 64%, 18%);
  --text-color: hsl(var(--hue-color), 24%, 35%);
  --text-color-light: hsl(var(--hue-color), 8%, 60%);
  --input-color: hsl(var(--hue-color), 24%, 97%);
  --body-color: hsl(var(--hue-color), 100%, 99%);
  --white-color: #fff;
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 75%);
  /* ============ Font and typography ============ */
  --body-font: 'Zona Pro', sans-serif;
  --title-font: 'Raleway', sans-serif;
  --biggest-font-size: 2.5rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
  /* ============ Font weight ============ */
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-thin: 200;
  /* ============ Margenes Bottom ============ */
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /* ============ z index ============ */
  --z-tooltip: 10;
  --z-fixed: 100;
  /* ============ Hover overlay ============ */
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
  --box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.slide-btn {
  position: relative;
  padding: 0 40px;
  transition: all ease 0.3s;
  box-shadow: var(--box-shadow);
}
.slide-btn::after {
  position: absolute;
  top: 50%;
  right: 0.6em;
  transform: translateY(-50%);
  content: "\BB";
  font-size: 1.2em;
  transition: all ease 0.3s;
  opacity: 0;
}
.slide-btn:hover {
  padding: 0 60px 0 20px;
}
.slide-btn:hover::after {
  right: 1.2em;
  opacity: 1;
}
html {
  --antd-wave-shadow-color: var(--first-color);
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: var(--body-font);
}
::selection {
  background: var(--second-color);
}
h1 {
  font-weight: 800;
  text-transform: capitalize;
  color: var(--first-color-alt);
}
h2 {
  font-weight: 600;
  text-transform: capitalize;
  color: var(--first-color-alt);
}
h3 {
  color: rgba(0, 0, 0, 0.95);
}
/* ================BUTTONS============== */
.ant-btn {
  border-radius: 12px;
  transition: ease 0.3s;
  box-shadow: var(--box-shadow);
}
.ant-btn-primary {
  background-color: var(--first-color);
  color: var(--white-color);
  font-weight: var(--font-semi-bold);
  transition: 0.3s;
  border: none;
  box-shadow: var(--box-shadow);
}
.ant-btn:hover,
.ant-btn:focus {
  cursor: pointer;
  border-color: var(--first-color);
  color: var(--first-color);
}
.ant-btn-primary:hover,
.ant-btn-primary:focus {
  cursor: pointer;
  background-color: var(--first-color-alt);
  color: var(--white-color);
  border: none;
}
/* ================INPUT============== */
.ant-input:hover,
.ant-input:focus,
.ant-select-focused:not(.ant-select-disabled).ant-select:not(.ant-select-customize-input) .ant-select-selector,
.ant-select:not(.ant-select-disabled):hover .ant-select-selector {
  border-color: var(--first-color) !important;
  box-shadow: 0 0 0 2px var(--first-color-shadow);
}
.ant-input-affix-wrapper:hover,
.ant-input-affix-wrapper-focused {
  border-color: var(--first-color) !important;
  box-shadow: 0 0 0 2px var(--first-color-shadow) !important;
}
.ant-input-affix-wrapper:hover .ant-input:hover,
.ant-input-affix-wrapper-focused .ant-input:hover {
  border: none;
  box-shadow: none;
}
/* ================MENU============== */
.ant-menu-item-selected,
.ant-menu-item-selected a,
.ant-menu-item-selected a:hover,
.ant-menu-light .ant-menu-item:hover,
.ant-menu-light .ant-menu-item-active,
.ant-menu-light .ant-menu-item-active a,
.ant-menu-light .ant-menu:not(.ant-menu-inline) .ant-menu-submenu-open,
.ant-menu-light .ant-menu-submenu-active,
.ant-menu-light .ant-menu-submenu-title:hover {
  color: var(--first-color);
}
.ant-menu-submenu-selected,
.ant-menu-submenu:hover > .ant-menu-submenu-title > .ant-menu-submenu-expand-icon,
.ant-menu-submenu:hover > .ant-menu-submenu-title > .ant-menu-submenu-arrow {
  color: var(--first-color);
}
.ant-menu-item:focus-visible,
.ant-menu-submenu-title:focus-visible {
  box-shadow: 0 0 0 2px var(--first-color-shadow) !important;
}
.ant-menu-item:active,
.ant-menu-submenu-title:active,
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: var(--first-color-shadow);
}
.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  border-right: 3px solid var(--first-color);
}
/* ================MODAL============== */
.ant-modal {
  width: 600px !important;
}
.ant-modal-header {
  border-radius: 14px 14px 0 0;
}
.ant-modal-content {
  border-radius: 14px;
}
.error {
  color: #d64a4a;
}
.ant-card-meta-title {
  overflow: visible;
  text-overflow: clip;
  white-space: pre-line;
}
.ant-card-actions li span:hover {
  color: var(--first-color);
}
a.ant-typography,
.ant-typography a {
  color: var(--second-color);
}
/* ================SPIN============== */
.ant-spin-text {
  color: var(--first-color);
}
/* ================CARD ICON============== */
.ant-card-actions > li > span a:not(.ant-btn):hover,
.ant-card-actions > li > span > .anticon:hover {
  color: var(--second-color);
  cursor: pointer;
}
/* ================POPOVER============== */
.ant-popover-inner {
  border-radius: 12px;
}
.ant-popover-inner-content {
  width: 240px;
}
.ant-popover-placement-left .ant-popover-inner-content {
  width: 260px;
}
.ant-form-item-explain,
.ant-form-item-extra {
  font-size: 12px;
}
.ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
  color: var(--second-color);
}
.ant-radio-checked::after {
  border: 1px solid var(--first-color);
}
.ant-radio-wrapper:hover .ant-radio,
.ant-radio:hover .ant-radio-inner,
.ant-radio-input:focus + .ant-radio-inner,
.ant-radio-checked .ant-radio-inner {
  border-color: var(--first-color);
}
.ant-radio-inner::after {
  background-color: var(--first-color);
}
.ant-form-item-has-error :not(.ant-input-disabled):not(.ant-input-borderless).ant-input:focus,
.ant-form-item-has-error :not(.ant-input-affix-wrapper-disabled):not(.ant-input-affix-wrapper-borderless).ant-input-affix-wrapper:focus,
.ant-form-item-has-error :not(.ant-input-disabled):not(.ant-input-borderless).ant-input-focused,
.ant-form-item-has-error :not(.ant-input-affix-wrapper-disabled):not(.ant-input-affix-wrapper-borderless).ant-input-affix-wrapper-focused {
  border-color: #ff7875 !important;
}
.ant-pagination-item-active {
  background: var(--light-color);
  border-color: var(--first-color-alt);
}
.ant-pagination-item-active a {
  color: var(--first-color-alt);
}
.ant-pagination-item-active:focus-visible,
.ant-pagination-item-active:hover {
  border-color: var(--first-color-alt);
  color: var(--first-color-alt);
}
.ant-pagination-options .ant-select:not(.ant-select-customize-input) .ant-select-selector {
  background: var(--light-color);
  border-color: var(--first-color-alt);
}
/* ================TABLE============== */
.ant-table-thead {
  border-radius: 10px 10px 0 0;
}
.ant-table-thead > tr > th {
  background: var(--first-color);
  color: rgba(255, 255, 255, 0.9);
  font-weight: var(--font-semi-bold);
  border-bottom: 1px solid var(--first-color-alt);
  text-transform: capitalize;
}
.ant-table-thead > tr > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
  background-color: rgba(255, 255, 255, 0.3);
}
.ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #e6ddc6;
}
.ant-table-tbody > tr.ant-table-row-selected > td {
  background: #e4ded1;
}
.ant-table.ant-table-small .ant-table-title,
.ant-table.ant-table-small .ant-table-footer,
.ant-table.ant-table-small .ant-table-thead > tr > th,
.ant-table.ant-table-small .ant-table-tbody > tr > td,
.ant-table.ant-table-small tfoot > tr > th,
.ant-table.ant-table-small tfoot > tr > td {
  color: var(--first-color);
}
.ant-btn-link,
.ant-table-filter-trigger.active {
  color: var(--first-color);
}
.ant-table-thead th.ant-table-column-sort,
.ant-table-thead th.ant-table-column-has-sorters:hover,
.ant-table-thead th.ant-table-column-has-sorters:focus {
  background: var(--first-color);
  color: rgba(255, 255, 255, 0.9);
}
.ant-table-row-expand-icon:focus,
.ant-table-row-expand-icon:hover {
  color: var(--first-color);
}
.ant-table-column-sorter-up.active,
.ant-table-column-sorter-down.active {
  color: #ffca28;
}
.ant-table-filter-column .ant-table-filter-trigger:hover,
.ant-table-filter-column .ant-table-filter-trigger.active {
  color: #ffca28;
}
.ant-table-filter-column .ant-table-filter-trigger.active .anticon-search {
  color: #ffca28 !important;
}
.ant-dropdown-menu-item-selected,
.ant-dropdown-menu-submenu-title-selected {
  color: var(--first-color);
  background-color: var(--first-color-shadow);
}
.ant-notification-notice-message {
  font-size: 15px;
}
.ant-notification-notice-description .ant-typography-text {
  font-size: 12px;
  color: var(--second-color);
}
.ant-notification-notice-description .ant-typography-text:hover {
  color: var(--first-color);
}
a {
  color: var(--second-color);
}
a:hover {
  color: #63adaf;
  text-decoration: underline;
}
.ant-checkbox-checked .ant-checkbox-inner {
  background-color: var(--first-color);
  border-color: var(--first-color);
}
.ant-checkbox-wrapper:hover .ant-checkbox-inner,
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-input:focus + .ant-checkbox-inner {
