@import '../../styles/mixins';

.admin-infir-mng-page {
  &-header {
    margin-bottom: 1.5rem;
    font-weight: var(--font-medium);
  }

  &-table-header {
    background-color: var(--white-color);
    height: 48px;
    margin-bottom: 15px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-shadow: var(--box-shadow);

    &-left {
      .role-selector {
        width: 200px;
        .ant-select-selector {
          border-radius: 9px;
        }
      }

      .search-bar {
        margin-left: var(--mb-0-25);
        width: 380px;

        .ant-input {
          border-radius: 10px 0 0 10px;
        }
        .ant-input-search-button {
          border-radius: 0 10px 10px 0 !important;
        }
      }
    }
  }

  .ant-table {
    box-shadow: var(--box-shadow);
    &-thead {
      font-size: 12px;
    }

    &-tbody {
      font-size: 13px;
    }

    .table-column-extra {
      font-size: 12px;
    }
  }

  &-footer {
    position: fixed;
    bottom: 30px;
    font-size: 12px;
    color: var(--first-color);
    font-weight: 200;
  }
}
